.navbar {
  position: fixed;
  top: 32px;
  left: 32px;
  z-index: 2;

  &__socials {
    display: flex;
    flex-direction: column;
    gap: 12px;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__social {
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    backdrop-filter: blur(10px);

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      transform: translateY(-2px);
      
      .navbar__icon {
        transform: scale(1.1);
      }
    }

    &:active {
      transform: translateY(0);
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: inherit;
  }

  &__icon {
    transition: transform 0.3s ease;
    fill: white;
  }
}

@media (max-width: 768px) {
  .navbar {
    top: 16px;
    left: 16px;

    &__socials {
      gap: 8px;
    }

    &__social {
      height: 40px;
      width: 40px;
    }

    &__icon {
      height: 14px;
      width: auto;
    }
  }
}

@media (max-width: 480px) {
  .navbar {
    top: 12px;
    left: 12px;

    &__socials {
      gap: 6px;
    }

    &__social {
      height: 36px;
      width: 36px;
    }

    &__icon {
      height: 12px;
      width: auto;
    }
  }
}

